export async function createBlock(formData: FormData) {
  const title = formData.get("title") as string;
  const code = formData.get("code") as string;
  const userId = parseInt(formData.get("userId") as string);

  await db.block.create({ data: { title, code, userId } });
  redirect(`/user/${userId}/blocks`);
}

async function deleteBlock(formData: FormData) {
  const blockId = parseInt(formData.get("blockId") as string);
  await db.block.delete({ where: { id: blockId } });
  redirect("/blocks");
}

async function editBlock(formData: FormData) {
  const blockId = parseInt(formData.get("blockId") as string);
  const title = formData.get("title") as string;
  const code = formData.get("code") as string;
  await db.block.update({ where: { id: blockId }, data: { title, code } });
  redirect("/blocks");
}
